import React, { useEffect, useState } from 'react'
import { Button, Col, DatePicker, Divider, Form, Input, Modal, notification, Row, Select, Space, Tooltip } from 'antd'
import { ALPHANUMBERIC_VALIDATE, ID_VALIDATE, MAIL_VALIDATE, NUMBER_VALIDATE } from '../../value/validate'
import { HttpUtils, URLUtils } from '../../utils'
import ErrorHandler from '../../utils/Error'
import { SafetyOutlined } from '@ant-design/icons'
import { ENUM_ROLE } from '../../value/model'
import moment from 'moment'

const { Option } = Select

const SubjectModal = (props) => {
  const { subjectInfo, modalType, fetchData, listRooms } = props
  const { visible, onCancel } = props
  const [form] = Form.useForm()
  const [formValues, setFormValues] = useState({});

  useEffect(() => {
    if (modalType === 'edit') {
      form.setFieldsValue({
        subjectId: subjectInfo.subjectId,
        firstName: subjectInfo.firstName,
        lastName: subjectInfo.lastName,
        email: subjectInfo.email,
        password: 'disabled',
        role: subjectInfo.role,
        majorId: subjectInfo.majorId.majorId,
        birthDate: moment(subjectInfo.birthDate),
        birthplace: subjectInfo.birthplace,
        phone: subjectInfo.phone,
        sex: subjectInfo.sex,
        idNo: subjectInfo.idNo,
        address: subjectInfo.address,
      })
    }
  }, [visible])

  const onSubmit = value => {
    value.startDate = value.startDate.format()
    if (modalType === 'edit') {
      editSubject(value)
    }
    else {
      createNewSubject(value)
    }
    onCancel()
  }

  const createNewSubject = (value) => {
    HttpUtils.post(URLUtils.buildBeURL('/subjects'), value)
      .then(resp => {
        notification.success({
          message: 'To mi thnh cng',
          placement: 'bottomRight'
        })
        fetchData()
      })
      .catch(err => {
        ErrorHandler.handle(err)
      })
  }

  const editSubject = (value) => {
    HttpUtils.put(URLUtils.buildBeURL(`/subjects/${subjectInfo.subjectId}`), value)
      .then(resp => {
        console.log(resp);
        notification.success({
          message: 'Chnh sa thnh cng',
          placement: 'bottomRight'
        })
        fetchData()
      })
      .catch(err => {
        ErrorHandler.handle(err)
      })
  }


  return (
    <>
      <Modal
        visible={visible}
        title={modalType === 'edit' ? "Chnh sa Mn Hc" : "To Mn Hc mi"}
        okText="OK"
        width={1000}
        cancelText="Hy"
        onCancel={() => {
          form.resetFields()
          onCancel()
        }}
        onOk={() => {
          form
            .validateFields()
            .then(value => {
              onSubmit(value)
              // form.resetFields()
            })
            .catch(info => {
              console.log('Validate Failed:', info)
            })
        }}
        forceRender
      >
        <Form form={form} layout="vertical" name="subject-form" onValuesChange={(_, values) => setFormValues(values)}>
          <Row gutter={[40, 10]}>
            <Col span={12}>
              <Form.Item
                label="M Mn Hc"
                name="subjectId"
                rules={ID_VALIDATE}
              >
                <Input />
              </Form.Item>
            </Col>
            <Col span={12}>
              <Form.Item
                label="Phng"
                name="roomId"
                rules={[{
                  required: true,
                  message: 'Vui lng chn phng.',
                }]}
              >
                <Select
                  placeholder="Chn phng hc"
                  size='large'
                  style={{
                    borderRadius: '6px',
                    fontSize: '14px'
                  }}
                >
                  {listRooms.map(item => (
                    <Option value={item.roomId}>{`${item.roomId} - ${item.slots} ch`}</Option>
                  ))}

                </Select>
              </Form.Item>
            </Col>

            <Col span={12}>
              <Form.Item
                label="Tn Mn Hc"
                name="name"
                rules={ALPHANUMBERIC_VALIDATE}
              >
                <Input />
              </Form.Item>
            </Col>
            <Col span={12}>
              <Form.Item
                label="Hc K"
                name="semester"
                rules={ALPHANUMBERIC_VALIDATE}
              >
                <Input />
              </Form.Item>
            </Col>
            <Col span={12}>
              <Form.Item
                label="S Tn Ch"
                name="credits"
                rules={NUMBER_VALIDATE}
              >
                <Input />
              </Form.Item>
            </Col>
            <Col span={12}>
              <Form.Item
                label="S Ngy Hc"
                name="days"
                rules={MAIL_VALIDATE}
              >
                <Input />
              </Form.Item>
            </Col>
            <Col span={12}>
              <Form.Item
                label="Bui Hc"
                name="shift"
                rules={[{
                  required: true,
                  message: 'Vui lng chn bui hc.',
                }]}
                style={{
                  borderRadius: '6px',
                }}
              >
                <Select
                  size='large'
                  placeholder="Chn bui hc"
                  style={{
                    borderRadius: '6px',
                    fontSize: '14px'
                  }}
                >
                  <Option value={0}>Sng</Option>
                  <Option value={1}>Chiu</Option>
                </Select>
              </Form.Item>
            </Col>
            <Col span={12}>
              <Form.Item
                label="Ngy Hc Trong Tun"
                name="dayOfWeek"
                rules={[{
                  required: true,
                  message: 'Vui lng chn ngy hc.',
                }]}
                style={{
                  borderRadius: '6px',
                }}
              >
                <Select
                  size='large'
                  placeholder="Chn ngy hc"
                  style={{
                    borderRadius: '6px',
                    fontSize: '14px'
                  }}
                >
                  <Option key={"2"} value={'2'}>Th hai</Option>
                  <Option key={"3"} value={'3'}>Th ba</Option>
                  <Option key={"4"} value={'4'}>Th t</Option>
                  <Option key={"5"} value={'5'}>Th nm</Option>
                  <Option key={"6"} value={'6'}>Th su</Option>
                  <Option key={"7"} value={'7'}>Th by</Option>

                </Select>
              </Form.Item>
            </Col>


            <Col span={12}>
              <Form.Item
                label="Ngy Bt u"
                name="startDate"
                rules={[{
                  required: true,
                  message: 'Vui lng chn ngy bt u mn hc.',
                }]}
              >
                <DatePicker size='large' placeholder={'Chn ngy bt u'} style={{ width: '100%', borderRadius: '6px' }} />
              </Form.Item>
            </Col>
            <Col span={24}>
              <Divider>
                T L im Thnh Phn
              </Divider>
            </Col>

            <Col span={12}>
              <Form.Item
                label="im Chuyn Cn"
                name="percentDiligence"
                rules={NUMBER_VALIDATE}
              >
                <Input />
              </Form.Item>
            </Col>
            <Col span={12}>
              <Form.Item
                label="im Kim Tra"
                name="percentTest"
                rules={NUMBER_VALIDATE}
              >
                <Input />
              </Form.Item>
            </Col>
            <Col span={12}>
              <Form.Item
                label="im Thc Hnh"
                name="percentPractice"
                rules={NUMBER_VALIDATE}
              >
                <Input />
              </Form.Item>
            </Col>
            <Col span={12}>
              <Form.Item
                label="im Tiu Lun"
                name="percentSerminar"
                rules={NUMBER_VALIDATE}
              >
                <Input />
              </Form.Item>
            </Col>
            <Col span={12}>
              <Form.Item
                label="im Thi"
                name="percentExam"
                rules={NUMBER_VALIDATE}
              >
                <Input />
              </Form.Item>
            </Col>
          </Row>
        </Form>
      </Modal>
    </>

  )
}

export default SubjectModal